* {
    margin: 0;
    padding: 0;
    border: 0;
    font-family: 'Montserrat', sans-serif;
  }


.buttons{
    padding: 6px 5px 0px 0px;  
}

body{
    margin-bottom: 25px;
    background-color: #FAFAFA;
}

main{
    background-color: #FFF;
    margin: 25px 0px 25px 0px;
}

.header{
    background-color: #90A4AE;
}

.logo{
    color: #fff;
    font-weight: 550;
}

.homePhoto{
    max-width: 100%;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.sign{
    width: 250px;
    padding: 10px;
}
.thumbnail{
    margin-top: 10px; 
    width: 100%;
    height: 320px;
}
.imgThumbnail{
    width: 100%;
    height: 250px;
}

.upperCase{
    text-transform: uppercase;
}

.card {
    min-height: 250px;
}

.table-responsive {
    overflow: visible !important;
    position: static !important;

  }

.advPhoto{
    padding: 0;
    width: 100%;
}
.propertyPhoto{
    padding: 0;
    width: 100%;
    max-height: 600px;
    min-height: 600px;

}
@media only screen and (max-width: 600px) {
    .propertyPhoto{
        width: 100%;
        max-height: 300px;
        min-height: 300px;
    }
}
@media only screen and (max-width: 400px) {
    .propertyPhoto{
        width: 100%;
        max-height: 150px;
        min-height: 150px;
    }
    .thumbnail{

        width: 100%;
        height: 265px;
    }
    .imgThumbnail{
        width: 100%;
        height: 200px;
    }
}



